@import "~styles/variables.less";

.main-things {
  display: flex;
  flex-direction: row;
  justify-content: center;
  .test-color {
    height: 300px;
    width: 300px;
    text-align: center;
    align-items: center;
    font-size: 20px;
  }
  :nth-child(1) {
    background-color: @primary-color;
  }
  :nth-child(2) {
    background-color: @secondary-color;
  }
  :nth-child(3) {
    background-color: @primary-color-shade;
  }
  :nth-child(4) {
    background-color: @success-color;
  }
  :nth-child(5) {
    background-color: @info-color;
  }
  :nth-child(6) {
    background-color: @default-color;
  }
  :nth-child(7) {
    background-color: @warning-color;
  }
  :nth-child(8) {
    background-color: @danger-color;
  }
  :nth-child(9) {
    background-color: @text-color-light;
  }
}

@primary-color: #4F79E6;@secondary-color: #FDC6E8;@primary-color-shade: lighten(@primary-color, 47%);@success-color: #6DC44A;@info-color: #02B1F7;@default-color: #bbbbbb;@warning-color: #EAD831;@danger-color: #FF8486;@text-color-light: lightslategrey;